<template>
  <div>
    <h1>音訊聊天室</h1>
    <p>Your ID: {{ myId }}</p>
    <input v-model="targetId" placeholder="輸入對方的 ID">
    <button @click="connectToPeer">連接</button>
    <button @click="startAudioChat">開始聊天</button>
    <button @click="stopAudioChat">結束聊天</button>
  </div>
</template>

<script>
import Peer from "peerjs";

export default {
  data() {
    return {
      peer: null,
      conn: null,
      mediaConnection: null,
      myId: null,
      targetId: "",
      localStream: null,
    };
  },
  mounted() {
    this.peer = new Peer(); // 初始化 PeerJS 客戶端
    this.peer.on("open", (id) => {
      this.myId = id; // 記錄自己的 ID
    });

    // 接收音訊連線
    this.peer.on("call", (call) => {
      navigator.mediaDevices
        .getUserMedia({ audio: true })
        .then((stream) => {
          call.answer(stream); // 回應並傳送自己的音訊流
          call.on("stream", (remoteStream) => {
            this.playAudio(remoteStream); // 播放對方音訊
          });
        });
    });
  },
  methods: {
    connectToPeer() {
      this.conn = this.peer.connect(this.targetId); // 連接到對方 ID
      this.conn.on("open", () => {
        console.log("已連接到對方！");
      });
    },
    startAudioChat() {
      navigator.mediaDevices
        .getUserMedia({ audio: true })
        .then((stream) => {
          this.localStream = stream;
          const call = this.peer.call(this.targetId, stream); // 撥打音訊電話
          call.on("stream", (remoteStream) => {
            this.playAudio(remoteStream); // 播放對方音訊
          });
          this.mediaConnection = call;
        });
    },
    stopAudioChat() {
      if (this.mediaConnection) {
        this.mediaConnection.close(); // 關閉音訊連線
      }
      if (this.localStream) {
        this.localStream.getTracks().forEach((track) => track.stop()); // 停止音訊
      }
    },
    playAudio(stream) {
      const audio = new Audio();
      audio.srcObject = stream;
      audio.play();
    },
  },
};
</script>